import Burger from "assets/images/burger.svg";
import Search from "assets/images/search.svg";
import BackArrow from "assets/images/back-arrow.svg";
import Cancel from "assets/images/cancel.svg";
import { useState } from "react";
import { SearchStore } from "stores/SearchStore";
import styled from "styled-components";
import { MapglStore } from "stores/MapglStore";

const SearchInputWrapper = styled.div`
  position: absolute;
  z-index: 2;
  width: 95%;
  height: 48px;
  background: #ffffff;
  border-radius: 30px;
  border: #fffff;
  display: flex;
  margin: 0 auto;
  margin-top: 50px;
  left: 0;
  right: 0;
  margin-top: 50px;
`;

const BurgerWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 40px;
  background: rgba(33, 33, 33, 0.08);
  border-radius: 20px;
  margin: 4px;
`;

const BurgerIcon = styled.div`
  background-image: url(${Burger});
  width: 18px;
  height: 12px;
`;

const SearchIcon = styled.div`
  background-image: url(${Search});
  width: 18px;
  height: 18px;
  margin-left: 7px;
  margin-top: 15px;
`;

const Input = styled.input`
  height: 24px;
  width: 190px;
  margin-left: 27px;
  margin-top: 10px;
  border: 0;
`;

const BackArrowIcon = styled.div`
  background-image: url(${BackArrow});
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  margin-left: 7px;
  margin-top: 15px;
`;

const CancelIcon = styled.div`
  background-image: url(${Cancel});
  width: 14px;
  height: 14px;
  margin-left: 7px;
  margin-top: 17px;
`;

interface SeacrhInputProps {
  searchStore: SearchStore;
  mapglStore: MapglStore;
  setOpenObjectCard: (value: boolean) => void;
  setIsEnterPressed: (value: boolean) => void;
  setIsResultsOpen: (value: boolean) => void;
  searchInputRef: React.RefObject<HTMLInputElement>;
  destroyAll: () => void;
}

export const SearchInput = ({
  searchStore,
  mapglStore,
  searchInputRef,
  setOpenObjectCard,
  setIsEnterPressed,
  setIsResultsOpen,
  destroyAll,
}: SeacrhInputProps) => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isInputFocused, setIsInputFocused] = useState<boolean>(false);
  const [inputValue, setInputValue] = useState("");

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    if (searchInputRef.current?.value) {
      searchInputRef.current && setInputValue(searchInputRef.current?.value);
      searchStore.setSearchValue(searchInputRef.current?.value);
    }

    searchStore.getSuggestions(
      event.target.value,
      mapglStore.myLocation as [number, number]
    );
  };

  return (
    <SearchInputWrapper
      onKeyDown={(event) => {
        if (event.key === "Enter") {
          if (searchInputRef.current?.value) {
            searchStore
              .getSearchResultsByQuery(
                searchInputRef.current?.value,
                mapglStore.myLocation as [number, number]
              )
              .then(() => {
                setIsEnterPressed(true);
              });
          }
        } else {
          setIsEnterPressed(false);
        }
      }}
      style={{
        justifyContent: inputValue ? "space-around" : "",
      }}
      onClick={() => {
        searchInputRef.current && searchInputRef.current.focus();
      }}
    >
      {inputValue === "" && (
        <BurgerWrapper>
          <BurgerIcon></BurgerIcon>
        </BurgerWrapper>
      )}
      {inputValue === "" && <SearchIcon></SearchIcon>}
      <div style={{ display: "flex" }}>
        {inputValue && (
          <BackArrowIcon
            onClick={() => {
              if (searchInputRef?.current?.value && searchStore.searchValue) {
                searchInputRef.current.value = searchStore.searchValue;
              }
              setOpenObjectCard(false);
            }}
          />
        )}
        <Input
          placeholder="Search"
          onChange={handleChange}
          ref={searchInputRef}
          onFocus={() => {
            setIsInputFocused(true);
            setIsResultsOpen(true);
            setOpenObjectCard(false);
            setIsEnterPressed(false);
            destroyAll();
          }}
          style={{
            marginLeft: inputValue ? "24px" : "27px",
          }}
          onBlur={() => setIsInputFocused(false)}
        ></Input>
      </div>
      {inputValue && (
        <CancelIcon
          onClick={() => {
            if (searchInputRef.current?.value) {
              searchInputRef.current.value = "";
              destroyAll();
              setOpenObjectCard(false);
              setIsResultsOpen(false);
            }
          }}
        />
      )}
    </SearchInputWrapper>
  );
};
