import {FC, useState} from "react";
import {SearchStore} from "stores/SearchStore";
import Swipe from "react-easy-swipe";
import {Places} from "../assets/data/places";
import {CompaniesList} from "./CompaniesList";
import {ResponseObjectTypes} from "../enums/ResponseObjectTypes.enum";
import {BuildingCard} from "./BuildingCard";
import {AdministrativeDivisionCard} from "./AdministarativeDivisionCard";
import {
    ArrowDownIcon,
    Divider,
    IconButton,
    IconButtonContainer,
    PlaceInfo,
    PlaceInfoWrapper,
    PlaceTitle,
    ShareIcon,
    SwipeIndicator,
    UpperInfo,
    Wrapper
} from "../styles/cardStyles";
import {OrganizationCart} from "./OrganizationCart";
import {observer} from "mobx-react";
import {MapglStore} from "stores/MapglStore";
import {PostMessageTypeEnum} from "../enums/PostMessageType.enum";
import {SendData} from "../utils/SendData";
import {PostMessageData} from "../api/interfaces/postMassage.interface";

interface ObjectCardProps {
    searchStore: SearchStore;
    mapglStore: MapglStore;
    routTime?: string;
    setIsDirectionCard: (value: boolean) => void;
}

export const ObjectCard: FC<ObjectCardProps> = observer(
    ({
         searchStore,
         mapglStore,
         routTime,
         setIsDirectionCard,
     }) => {
        const [isOpen, setIsOpen] = useState(false);
        const [isCompaniesListOpen, setCompaniesListIsOpen] = useState(false);

        const canBeSwiped =
            searchStore.currentObject?.type === ResponseObjectTypes.branch ||
            searchStore.currentObject?.type === ResponseObjectTypes.building;

        const setType = (type?: string) => {
            for (let item of Places) {
                if (type === item.type) {
                    return item.title;
                }
            }
            return "";
        };

        const shareData = (data: PostMessageData) => {
            if (PostMessageTypeEnum.SHARE_INFO === data.type) {
                const twoGisLink = `https://2gis.com.cy/cyprus/${searchStore.currentObject?.type === ResponseObjectTypes.branch ? 'firm' : 'geo'}/${data.data}`;
                const googleMapsLink = `https://www.google.com/maps/search/?api=1&query=${searchStore.currentObject?.point.lat}%2C${searchStore.currentObject?.point.lon}`;
                SendData({type: PostMessageTypeEnum.SHARE_INFO,
                    data: `For NaviCY:\n${twoGisLink}\n\nFor Google maps:\n${googleMapsLink}`})
            } else {
                SendData(data);
            }
        }

        const cardHeight = searchStore.currentObject?.floors || searchStore.currentObject?.links?.branches?.count ? "28%" : "21%"

        return (
            <>
                <Wrapper
                    style={{
                        display: searchStore.currentObject ? "flex" : "none",
                        height: isOpen ? "95%" : cardHeight,
                    }}
                >
                    <Swipe
                        onSwipeUp={() => {
                            setIsOpen(canBeSwiped);
                        }}
                        onSwipeDown={() => {
                            setIsOpen(false);
                        }}
                    >
                        <IconButtonContainer
                            onClick={() => {
                                setIsDirectionCard(true);
                            }}
                        >
                            <IconButton
                                style={{
                                    top: isOpen ? "75vh" : "-35px",
                                    transition: "top 0.3s",
                                }}
                            />
                        </IconButtonContainer>
                        {canBeSwiped ? <SwipeIndicator/> : null}
                        <UpperInfo
                            style={{
                                flexDirection: isOpen ? "row" : "column",
                                justifyContent: isOpen ? "space-around" : "",
                                alignItems: isOpen ? "center" : "",
                            }}
                        >
                            {isOpen ? (
                                <ArrowDownIcon onClick={() => setIsOpen(false)}/>
                            ) : null}
                            <PlaceTitle>
                                {searchStore.currentObject?.name
                                    ? searchStore.currentObject?.name
                                    : "Utility structure"}
                            </PlaceTitle>
                            {!isOpen ? (
                                <PlaceInfoWrapper>
                                    <PlaceInfo>
                                        {searchStore.currentObject?.subtype ||
                                        searchStore.currentObject?.purpose_name ||
                                        setType(searchStore.currentObject?.type)}
                                    </PlaceInfo>
                                    <PlaceInfo>{routTime}</PlaceInfo>
                                </PlaceInfoWrapper>
                            ) : (
                                <ShareIcon
                                    onClick={() => shareData({
                                        type: PostMessageTypeEnum.SHARE_INFO, data: searchStore.currentObject ?
                                            searchStore.currentObject.type === ResponseObjectTypes.building ? searchStore.currentObject?.address.building_id : searchStore.currentObject.id
                                            : ""
                                    })}
                                    style={{marginLeft: "11px"}}
                                />
                            )}
                        </UpperInfo>
                        <Divider/>
                    </Swipe>
                    {searchStore.currentObject?.type === ResponseObjectTypes.building ? (
                        <BuildingCard
                            shareData={shareData}
                            isOpen={isOpen}
                            searchStore={searchStore}
                            mapglStore={mapglStore}
                            coordinates={[searchStore.currentObject.point?.lon, searchStore.currentObject.point?.lat]}
                            setCompaniesListIsOpen={setCompaniesListIsOpen}
                        />
                    ) : null}
                    {searchStore.currentObject?.type === ResponseObjectTypes.admDiv ||
                    searchStore.currentObject?.type === ResponseObjectTypes.street ? (
                        <AdministrativeDivisionCard
                            shareData={shareData}
                            isOpen={isOpen}
                            searchStore={searchStore}
                            mapglStore={mapglStore}
                            coordinates={[searchStore.currentObject.point?.lon, searchStore.currentObject.point?.lat]}
                        />
                    ) : null}
                    {searchStore.currentObject?.type === ResponseObjectTypes.branch ? (
                        <OrganizationCart
                            shareData={shareData}
                            setSwipeIsOpen={setIsOpen}
                            mapglStore={mapglStore}
                            searchStore={searchStore}
                            isSwipeOpen={isOpen}
                            setCompaniesListIsOpen={setCompaniesListIsOpen}
                            coordinates={[searchStore.currentObject.point?.lon, searchStore.currentObject.point?.lat]}
                        />
                    ) : null}
                </Wrapper>
                {isCompaniesListOpen && (
                    <CompaniesList setIsOpen={setCompaniesListIsOpen}/>
                )}
            </>
        );
    }
);
