import { useMapglStore } from "hooks/store";
import { useState } from "react";

export const ChangeLocation = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [lon, setLon] = useState("");
  const [lat, setLat] = useState("");

  const mapglStore = useMapglStore();

  const handleSubmit = (event: React.SyntheticEvent) => {
    event.preventDefault();
    mapglStore.setLocationMarker([+lon, +lat]);
    
    setIsOpen(false);
  };
  return (
    <div style={{ position: "absolute", zIndex: 999 }}>
      {isOpen ? (
        <form onSubmit={handleSubmit}>
          <input placeholder="lon" onChange={(e) => setLon(e.target.value)} />
          <input placeholder="lat" onChange={(e) => setLat(e.target.value)} />
          <input type="submit" value="Submit" />
        </form>
      ) : (
        <button onClick={() => setIsOpen(true)}>Change location</button>
      )}
    </div>
  );
};
